import React from 'react';
import TableCell from '@mui/material/TableCell';
import TableHeader from '@crema/core/AppTable/TableHeader';
import {useIntl} from 'react-intl';
const TableHeading = () => {
  const {messages} = useIntl();

  return (
    <TableHeader>
      <TableCell>{messages['OrderID']}</TableCell>
      <TableCell align='left'>{messages['Customer']}</TableCell>
      <TableCell align='left'>{messages['OrderDate']}</TableCell>
      <TableCell align='left'>{messages['ecommerce.addproduct.price']}</TableCell>
      <TableCell align='left'>{messages['paymentMethod']}</TableCell>
      <TableCell align='left'>{messages['OrderStatus']}</TableCell>
      <TableCell align='right'>{messages['Actions']}</TableCell>
    </TableHeader>
  );
};

export default TableHeading;
