import React, {useEffect, useState} from 'react';
import OrderTable from './OrderTable';
import AppsContainer from '../../../@crema/core/AppsContainer';
import {useIntl} from 'react-intl';
import {useDispatch, useSelector} from 'react-redux';
import {getRecentOrders, resetOrder} from '../../../redux/actions';
import {Button, Hidden} from '@mui/material';
import AppsHeader from '../../../@crema/core/AppsContainer/AppsHeader';
import AppsContent from '../../../@crema/core/AppsContainer/AppsContent';
import AppsPagination from '../../../@crema/core/AppsPagination';
import Box from '@mui/material/Box';
import AppInfoView from '../../../@crema/core/AppInfoView';
import AppSearchBar from '../../../@crema/core/AppSearchBar';
import {useNavigate} from 'react-router-dom';

const InProgressOrders = () => {
  const {messages} = useIntl();
  const dispatch = useDispatch();
  const recentOrders = useSelector(({ecommerce}) => ecommerce.recentOrders);
  const orderCount = useSelector(({ecommerce}) => ecommerce.orderCount);
  const lastVisible = useSelector(({ecommerce}) => ecommerce.lastVisible);
  const [page, setPage] = useState(0);
  const [search, setSearchQuery] = useState('');
  const [index, setIndex] = useState('');
  const [searchOrder, setSearchOrderQuery] = useState('');
  const navigation = useNavigate();

  const hanedlAddOrder = () => {
    navigation('/Orders/AddOrder');
  };
  useEffect(() => {
    return () => {
      dispatch(resetOrder());
    };
  }, []);
  const onPageChange = (event, value) => {
    setIndex(lastVisible);

    setPage(value);
  };
  useEffect(() => {
    setSearchQuery('In Progress');
    dispatch(getRecentOrders(searchOrder,search, page, index));
  }, [dispatch,searchOrder, search, page, index]);

  const onSearchOrder = (e) => {
    setSearchOrderQuery(e);

    setPage(0);
  };
  return (
    <>
      <AppsContainer
        title={messages['sidebar.orders.inProgressOrdres']}
        fullView
      >
        <AppsHeader>
          <Box
            display='flex'
            flexDirection='row'
            alignItems='center'
            width={1}
            justifyContent='space-between'
          >
            <AppSearchBar
              iconPosition='right'
              overlap={false}
              onChange={(event) => onSearchOrder(event.target.value)}
              placeholder={messages['common.searchHere']}
            />
            <Box display='flex' flexDirection='row' alignItems='center'>
              <Button variant='contained' color='primary' onClick={hanedlAddOrder}>
              {messages['sidebar.management.AddOrder']}
              </Button>

              <Hidden smDown>
                <AppsPagination
                             rowsPerPage={40}

                  count={orderCount}
                  page={page}
                  onPageChange={onPageChange}
                />
              </Hidden>
            </Box>
          </Box>
        </AppsHeader>

        <AppsContent
          sx={{
            paddingTop: 2.5,
            paddingBottom: 2.5,
          }}
        >
          <OrderTable orderData={recentOrders} />
        </AppsContent>

        <Hidden smUp>
          <AppsPagination
                       rowsPerPage={40}

            count={orderCount}
            page={page}
            onPageChange={onPageChange}
          />
        </Hidden>
      </AppsContainer>
      <AppInfoView />
    </>
  );
};

export default InProgressOrders;
